.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 90%;
    background-color: rgb(40, 1, 1);
    height: 80vh;
    margin: 4rem auto;
    border-radius: 20px;
    box-shadow: 0 0 25px rgba(0,0,0, 0.2);
  
    .heading{
      line-height: 1.3;
      font-size: 45px;
      color: #ffffff;
      font-weight: 450;
      border-bottom: 2px solid #fff;
      letter-spacing: 1px;
      padding: .6rem;
      font-family:  -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
    .int{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 90%;
      padding: 1rem;
      
      .ints{
          padding: 0.3rem;
          width: 60%;
          height: 40px;       
          border: none;
          border-bottom: 1px solid rgba(0, 0, 0, 0.3);
          border-radius: 10px;
          box-shadow: 0 0 15px 1px rgba(173, 173, 173, 0.3);
          transition: all .3s ease-in-out;
          ::placeholder{
              color: gray;
              font-size: 19px;
          }
  
          &:hover{
            transform: scaleX(.97);
            box-shadow: 0 0 10px .3px rgba(173, 173, 173, 0.2);
          }
  
          @media screen and (max-width: 1200px) {
              width: 95%;
          }
      }
      .ints:last-child{
          margin-top: 1rem;
      }
  
      
      
    }
    .btn {
      padding: 0.5rem 1.5rem;
      font-size: 1.3rem;
      font-weight: 500;
      border: none;
      margin-top: 1rem;
      background: rgba(9, 9, 232, 0.85);
      color: white;
      border-radius: 13px;
      letter-spacing: 1px;
      cursor: pointer;
      transition: all 0.3s ease-in-out;
      box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.6);
     
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  
      &:hover {
        transform: scaleX(.97);
        filter: brightness(70%);
        box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
      }
    }
    
  }
  